.container {
  margin: 0;
  padding: 0;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: rgb(0, 0, 0);
  display: flex;
  height: 80vh;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
}
.card {
  color: #fff;
  text-align: center;
  background-color: #151515; /*Background color of the card. We have to set one so the gradient background does not show through allowing for the border effect.*/
  padding: 0.5em;
  border-radius: 8px; /*Rounds the corners a little.*/
  position: relative;
  margin: 0 0.6em; /*Position of relative because we will use position of absolute on the pseudo-element. This will make it so that the pseudo-element will not float away from the card.*/
}
.card-plan {
  font-size: 0.8em;
  margin-bottom: 0.5em;
}
/*Creating the gradient border with a pseudo-element. */
.card::before {
  content: ""; /*Set the content property to an empty string, if you don't set a content the element won't show.*/
  position: absolute; /*Allows for this element to be freely moved within the parent box that has relative position.*/
  top: 0;
  left: 0;
  width: calc(
    100% + 17px
  ); /* We make this element 100% of the card with and height and add 8px so make it slightly larger.*/
  height: calc(100% + 17px);
  transform: translate(
    -8px,
    -8px
  ); /*Now we use a negative translation to center this element in terms of the card. We use half of the extra 8px we gave it for width and height.*/
  background: linear-gradient(
      60deg,
      #5ff281,
      #5f86f2,
      #a65ff2,
      #f25fd0,
      #f25f61,
      #f2cb5f
    )
    0 50%; /*You can use a gradient of your choosing. I generated this one online.*/
  background-size: 300% 300%; /*Setting the background size to a large number since we will be animating the background position via keyframes.*/
  animation: gradient-animation 2.5s alternate infinite; /*Applying the keyframe animation.*/
  border-radius: 8px; /*Rounds the corners a little.*/
  z-index: -1; /*Sends the the gradient background to the back of the card.*/
}
.card-title {
  font-size: 0.8rem;
  text-align: justify;
  margin-bottom: 2em;
}
.card-title2 {
  font-size: 1rem;
  text-align: center;
  margin-bottom: 1em;
  color: #833ab4;
}
.card-title span {
  font-size: 16px;
}
.card-button {
  display: inline-block;
  font-size: 14px;
  color: #fff;
  text-decoration: none;
  border: 1px solid #fff;
  border-radius: 20px;
  padding: 10px 15px;
}
/*
    The keyframe animation will have one stop at 50% where we change the background position from the default value to 100% on the X axis and 50% on the Y.
    We alternate this animation and make it infinite when we apply it to the element we want to animate.
*/
@keyframes gradient-animation {
  60% {
    background-position: 100% 50%;
  }
}
